.App {
	text-align: center;
	/* font-family: 'Open Sans', sans-serif; */
	/* letter-spacing: -3px; */
	font-family: 'Circular Std', 'Open Sans', sans-serif;
	font-weight: 900;
	font-style: normal;
	color: #333;
}
.subtitle {
	font-size: 1.3em;
}
select {
	appearance: none;
	background-color: #fff;
	border: none;
	padding: 1em;
	border-radius: 4px;
	text-transform: uppercase;
	font-weight: 500;
	font-size: 0.9em;
	font-family: inherit;
	z-index: 1;
	box-shadow: 0 1px 6px 1px rgba(0, 0, 0, 0.13);
}

.shadow {
	box-shadow: 0 1px 6px 1px rgba(0, 0, 0, 0.13);
}
.cover-photo {
	z-index: -1;
	position: fixed;
	bottom: 0;
	right: 0;
	/* width: 59em; */
	height: 50em;
	width: 50%;
	/* max-width: 60em; */
	height: 90vh;
	max-height: 100em;
	/* transform: translate(0vh,7vh); */
	background-size: cover;
	background-repeat: no-repeat;
}
.cover-photo-large {
	width: 70%;
}
.loading-div {
	background-color: #fff;
	text-transform: capitalize;
	text-align: left;
	overflow: hidden;
	width: 13em;
}
.loading-div-text {
	font-weight: 600;
	margin-left: 1em;
}

@keyframes loading-animation {
	0% {
		transform: translateX(-15em);
	}
	100% {
		transform: translateX(15em);
	}
}

.animated-bar {
	width: 15em;
	height: 4px;
	border-radius: 4px;
	animation: loading-animation 1s infinite;
	background-color: #80bdea;
}
.project-cards-container {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: flex-start;
	align-items: flex-start;
	align-content: space-between;
	width: 100%;
	margin-top: 2em;
}
.post-card .image-frame {
	background-position: center center;
	background-size: cover;
	width: 100%;
	height: 18em;
}
.project-card,
.post-card {
	font-weight: 400;
	flex: 1 1 auto;
	margin: 1em;
	margin-left: 0;
	width: 18em;
	max-width: 18em;
	border-radius: 5px;
	min-height: 270px;
	display: flex;
	flex-direction: column;
	z-index: 0;
	background: #ffffff;
	box-shadow: 0 1px 6px 1px rgba(0, 0, 0, 0.13);
	overflow: hidden;
}
.post-card {
	font-size: 0.9em;
	max-width: 20em;
}
.project-card .subtitle {
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
}

.project-card .top-section {
	/* padding-top: 0.6em; */
	padding-left: 1em;
	display: flex;
	padding-bottom: 0;
	padding-right: 0;
	background-color: #cbe0f0;
}
.project-card .top-section-image {
	background-image: url("./images/project-card-person.svg");
	background-size: contain;
	background-repeat: no-repeat;
	background-position: right bottom;
	width: 100%;
	margin-top: 0.6em;
	height: 90px;
}
.project-card .bottom-section {
	background-color: #fff;
	padding: 1em;
	text-align: left;
	flex-grow: 1;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}
.project-card .details {
	margin-top: 0.5em;
	margin-bottom: 0.5em;
	font-size: 1em;
	font-weight: 400;
	color: rgba(33, 33, 33, .6);
	width: 90%;
}
.padding-1 {
	padding-bottom: 1em;
}
div[class^="col"] {
	padding-left: 0 !important;
	padding-right: 0 !important;
}
.container {
	height: calc(100vh - 3em);
	width: 80vw !important;
	margin: auto;
	margin-top: 3em;
	/* padding-left: 0!important;
  padding-right: 0!important; */
}

.welcome {
	height: 80vh;
	padding: 0 20px;
	width: 80vw;
	display: flex;
	justify-content: center;
	align-items: center;
}
.welcome .photo {
	width: 4em;
	min-width: 4em;
	height: 4em;
	/* background-image: url("./images/upit-04271.jpg"); */
	background-image: url("./images/elefcodes.jpg");
	background-size: cover;
	background-position: 28%;
	border-radius: 50%;
	margin-right: 30px;
}
.container span.blue-box,
.welcome span.blue-box {
	color: #8bc5ed;
}
.title {
	max-width: 10em;
	text-align: left;
	font-size: 3.5em;
}

.container .title {
	/* margin-top: 0.7em; */
}
.main-photo {
	/* max-width: 48em; */
	min-height: 20em;
	max-height: calc(100vh - 20em);
	width: 100%;
	/* width: 68%; */
	/* width: 100%; */
	/* margin-left: 5em; */
	/* margin-top: -20px; */
}

.rounded-button {
	font-weight: 500;
	letter-spacing: 0.5px;
	font-size: 1.2em;
	padding: 13px 40px;
	text-decoration: none;
	color: #fff;
	background-color: #80bdea;
	border-radius: 40em;
	background: #80bdea;
	box-shadow: 0 1px 6px 1px rgba(128, 189, 234, 0.64);
	display: inline-block;
	text-overflow: ellipsis;

	/* Needed to make it work */
	overflow: hidden;
	white-space: nowrap;
	/* flex-direction:  */
	/* display: inline-block; */
	transition: all .2s;
}
.rounded-button:hover {
	color: #333;
}

.menu-container {
	width: 100%;
	height: 100vh;
	background: #fff;
	transform: translateX(0em);
	position: fixed;
	overflow: scroll;
	top: 0;
	left: 0;
	z-index: 1;
	transition: 0.4s;
}
.menu-container.disabled {
	width: 100%;
	/* transform: translateX(80%); */
	transform: translateX(10em);
	/* margin-left: 100%; */
	opacity: 0;
	z-index: 1;
}

.menu-container ul {
	list-style: none;
	padding: 0;
	margin: 0;
	padding: 2em 30%;
}
.menu-container ul li {
	transition: 0.2s;
	transform: translateX(0em);
}
.menu-container ul li a {
	text-decoration: none;
	color: inherit;
}
.menu-container ul li:hover {
	color: #80bdea;
	transform: translateX(0.5em);
}
.object {
	z-index: -1;
	height: 30em;
	width: 50em;
	position: absolute;
	right: 0;
	bottom: 0;
	display: flex;
	justify-content: flex-end;
	align-items: flex-end;
	background-size: cover;
	background-image: url("./images/path3.svg");
}
.object div {
	margin-top: 4em;
	margin-right: 2em;
	/* line-height: 1.4em; */
	font-size: 1.4em;
	align-self: center;
	max-width: 200px;
	color: #fff;
	text-align: left;
	font-weight: 500;
}
.object div span {
	font-weight: 900;
}
.object img {
	/* display: flex; */
	width: 16vw;
	height: 16vw;
	min-width: 14em;
	min-height: 14em;
	max-width: 17em;
	max-height: 17em;
	margin-right: 5vw;
	margin-bottom: 5em;
}
#some-text {
	text-align: left;
}
@media screen and (max-width: 500px) {
	.main-photo {
		min-height: 15em;
	}
	#some-text {
		text-align: center;
	}
	.title {
		font-size: 2.3em;
	}
	.post-card,
	.project-card {
		margin-right: 0;
	}
}
@media screen and (max-width: 600px) {
	.title {
		font-size: 2.5em;
	}
}

@media screen and (max-width: 850px) {
	.cover-photo {
		width: 20em;
		height: 25em;
		background-size: cover;
	}
	.container {
		margin-top: 2em;
		width: 98vw !important;
	}

	.project-cards-container {
		justify-content: center;
	}

	.welcome {
		margin-top: 2em;
		height: 20%;
		font-size: 1.7em;
		height: 100px;
	}
	.welcome .title {
		font-size: 0.8em;
	}
	.object {
		height: 300px;
		width: 100vw;
		background-position: top right;
	}
	.object img {
		min-width: 150px;
		min-height: 150px;
	}
	.object div {
		margin-left: 10px;
		margin-right: 1em;
		max-width: 150px;
		font-size: 1.1em;
	}
}
@media screen and (max-height: 600px) {
	/* .cover-photo {
    width: 15em;
    height: 20em;
  } */
}
@media screen and (max-width: 900px) and (orientation: landscape) {
	.welcome .title {
		color: #fff;
	}
	.welcome span.blue-box {
		color: #a4d7f9;
	}
	.object {
		height: 110vh;
	}
	.object div {
		align-self: flex-end;
		margin-bottom: 2em;
		max-width: 25vw;
		max-width: 12em;
	}

	.menu-container ul .title {
		font-size: 2em;
	}
	.menu-container ul .title:hover {
		color: #fff;
	}
	.menu-container ul {
		padding: 1.5em;
		padding-top: 2%;
		padding-left: 20%;
	}
}

/* BURGER MENU */

.burger {
	display: inline-block;
	cursor: pointer;
	position: absolute;
	top: 2em;
	right: 4%;
	z-index: 10;
}

.line {
	width: 2.9em;
	height: 6px;
	border-radius: 5px;
	background-color: #333;
	margin: 6px 0;
	transition: 0.4s;
}

.change#bar1 {
	-webkit-transform: rotate(-45deg) translate(-9px, 6px);
	transform: rotate(-45deg) translate(-9px, 7px);
}

.change#bar2 {
	opacity: 0;
	transform: translate(-10em, 0);
	/* display: none; */
}

.change#bar3 {
	-webkit-transform: rotate(45deg) translate(-8px, -8px);
	transform: rotate(45deg) translate(-9px, -8px);
}
/* END */
